.overlay {
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.overlay .modal-container {
  max-width: 600px;
  height: 150px;
  width: 100%;
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  box-shadow: 0px 0px 18px 0px rgba(0, 0, 0, 0.75);
  border-radius: 8px;
}

.overlay .modal-container .modal-content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.overlay .modal-container .modal-content p {
  margin: 8px 0px;
}

.overlay .modal-container .modal-content .order-content {
  border-top: 1px solid rgba(128, 128, 128, 0.449);
  border-bottom: 1px solid rgba(128, 128, 128, 0.449);
  padding: 17px 0px;
  width: 100%;
  text-align: center;
}

.overlay .modal-container .modal-content .modal-button {
  width: 100px;
  border: none;
  background-color: black;
  color: yellow;
  border-radius: 10px;
  padding: 4px;
  cursor: pointer;
}

.overlay .modal-container .close-icon {
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: pointer;
}
