.category-container {
  height: 50px;
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-bottom: 30px;
}

.category-container .category-button {
  background-color: white;
  border: none;
  font-size: 23px;
  font-weight: bold;
  cursor: pointer;
  padding: 10px;
  border-radius: 10px;
  transition: all 0.3s ease-in-out;
}

.category-container .category-button-active {
  background-color: black;
  color: yellow;
  border: none;
  font-size: 23px;
  font-weight: bold;
  cursor: pointer;
  padding: 10px;
  border-radius: 10px;
}

.category-container .category-button:hover {
  background-color: black;
  color: yellow;
}
